<!--
  select 
  @param {Object} data ,
    ex:
        {
          optsList: [{
            value: 0,
            label: 'value0'
          }, {
            value: 1,
            label: 'value1'
            // 
            render: function() {
                return <a>111</a>
            }
          }, {
            value: 2,
            label: 'value2',
            isDisabled: true
          }]
        }
  @param {Array} resultList ,,
  @param {Boolean} isDisabled 
  @param {Boolean} isOpened 
  @param {Boolean} hasSearch 
  @param {String} okText ,''
  @param {String} okCancel ,''
  @param {Function} onOk 
  @param {Function} onCancel 
  @param {Number | String | Function} filter ,
  @param {Object} appendClass class
  @param {Object} appendStyle Style
-->
<template>
    <pv-dropdown :data="data"
                 :is-disabled="isDisabled"
                 :is-opened="isOpened"
                 :has-search="hasSearch"
                 :result-list.sync="resultList"
                 :ok-text="okText"
                 :on-ok="onOk"
                 :on-cancel="onCancel"
                 :cancel-text="cancelText"
                 :append-style="appendStyle"
                 :filter="filter"
                 :as-label="asLabel"
                 :as-value="asValue"
                 :has-footer="true"
                 :is-multiple="true"
                 :append-class="appendClass">
        <slot></slot>
    </pv-dropdown>
</template>

<script>
    import {componentBaseParamConfig, alias} from '../base-config';
    import pvDropdown from '../dropdown';
    import pvButton from '../button';

    export default {
        props: Object.assign({}, componentBaseParamConfig, alias, {
            data: {
                type: Object,
                default: {
                    optsList: []
                }
            },
            isDisabled: {
                type: Boolean,
                default: false
            },
            isOpened: {
                type: Boolean,
                default: false
            },
            resultList: {
                type: Array,
                default() {
                    return [];
                }
            },

            okText: {
                type: String,
                default: ' '
            },

            cancelText: {
                type: String,
                default: ' '
            },

            onOk: {
                type: Function
            },

            onCancel: {
                type: Function
            },

            hasSearch: {
                type: Boolean,
                default: true
            },
            filter: {}
        }),

        components: {
            pvDropdown
        }
    }
</script>


