<!--
    dialog :
    @require button global-mask
    id
    ex: <v-dialog :id="'dialog1'" :ok-fn="dialogCallbackFn1" :cancel-fn="dialogCallbackFn2" :append-style="{width: '300px'}">
           
        </v-dialog>


    
    vm.$root.$$dialog.yourId.show();

    @param {String}     id ,dialogid,dialog
    @param {String}     title title
    @param {Boolean}    hasHeader 
    @param {Boolean}    hasFooter 
    @param {Boolean}    hasOkBtn 
    @param {Boolean}    hasCancelBtn 
    @param {Boolean}    hasCancelBtn 
    @param {Boolean}    isShow ,
    @param {String}     cancelText 
    @param {String}     okText 
    @param {Function}   onOk ok
    @param {Function}   onCancel cancel
    @param {Function}   onBeforeClose ,false
    @param {Object}     appendClass class
    @param {Object}     appendStyle Style
-->
<template>
    <div :class="['vc-dialog-wrap', {'vc-block': isShow, 'vc-hidden': !isShow}]" tabindex="10000" @keyup.esc="closeFn">
        <pv-mask :is-show="showMask"></pv-mask>
        <div :class="['vc-dialog vc-dialog-autoscroll', appendClass]" :style="appendStyle">
            <div class="vc-dialog-content">
                <!-- header -->
                <!-- close -->
                <a v-if="hasHeader" href="javascript:void(0);" class="vc-dialog-close" @click="closeFn">
                    <span class="vc-dialog-close-x"></span>
                </a>

                <div v-if="hasHeader" class="vc-dialog-header">
                    <h3 class="vc-dialog-title">{{title}}</h3>
                </div><!-- header end -->

                <!-- body -->
                <div class="vc-dialog-body">
                    <slot></slot>
                </div>

                <!-- footer -->
                <div v-if="hasFooter" class="vc-dialog-footer">
                    <pv-button v-if="hasCancelBtn" @click="_onCancel">{{cancelText}}</pv-button>
                    <pv-button v-if="hasOkBtn"
                              type='primary'
                              @keyup.enter="_onOk"
                              @click="_onOk">
                        {{okText}}
                    </pv-button>
                </div>
            </div>
        </div>
        <a class="shortcut" href="javascript:void 0" @keyup.esc.stop="closeFn" @keyup.enter.stop="_onOk"></a>
    </div>
</template>

<script>
    import {componentBaseParamConfig} from '../base-config';
    import pvMask from '../global-mask';
    import pvButton from '../button';

    export default {
        props: Object.assign({}, componentBaseParamConfig, {
            id: {
                type: String,
                required: true
            },
            title: {
                type: String,
                default: ''
            },
            hasHeader: {
                type: Boolean,
                default: true
            },
            hasFooter: {
                type: Boolean,
                default: true
            },
            hasCancelBtn: {
                type: Boolean,
                default: true
            },
            hasOkBtn: {
                type: Boolean,
                default: true
            },
            okText: {
                type: String,
                default: ' '
            },
            cancelText: {
                type: String,
                default: ' '
            },
            onOk: {
                type: Function
            },
            onCancel: {
                type: Function
            },
            onBeforeClose: {
                type: Function
            },
            isShow: {
                type: Boolean,
                default: false
            }
        }),

        compiled() {
            this.appendStyle = Object.assign({ width: '500px'}, this.appendStyle);
        },

        ready() {
            this.$root.$$dialog = this.$root.$$dialog || {};
            this.$root.$$dialog[this.id] = this;
        },

        data() {
            return {
                showMask: false
            }
        },

        watch: {
            isShow(val) {
                this.showMask = val;
            }
        },

        methods: {
            show() {
                const _this = this;
                this.isShow = true;
                // 
                setTimeout(function() {
                    _this.$el.querySelector('.shortcut').focus();
                }, 20);
            },
            hide() {
                this.isShow = false;
            },
            closeFn() {
                this.hide();
            },
            _onOk() {
                if(this.onBeforeClose && this.onBeforeClose() === false) {
                    return;
                }

                this.onOk && this.onOk();
                this.hide();
            },
            _onCancel() {
                if(this.onBeforeClose && this.onBeforeClose() === false) {
                    return;
                }

                this.onCancel && this.onCancel();
                this.hide();
            }
        },

        components: {
            pvButton,
            pvMask
        }
    }
</script>

<style lang="scss">
    @import "style.scss";
</style>


