<!--
  checkbox 
  @param {Array<Object>} data checkbox,
      ex: [{
          value: 0,
          label: 'value0'
        }, {
          value: 1,
          label: 'value1'
        }, {
          value: 2,
          label: 'value2',
          isDisabled: true
        }]

  @param {Array} resultList ,
  @param {value} value resultListvalue,resultListresultList[0]
  @param {Boolean} isVertical checkbox(false)(true),
  @param {Object} appendClass class
  @param {Object} appendStyle Style
  @param {String} asValue value key
  @param {String} asLabel label key
-->
<template>
  <div v-if="isShow" v-for="it in data" track-by="$index" :style="appendStyle" :class="[appendClass]">
    <label class="vc-label" :class="{'vc-label-checked': (resultList && resultList.includes(it.value) || value === true), 'vc-label-disabled': it.isDisabled}" @click="toggleSwitch($index, it.value)">
      <span class="vc-checkbox"></span>
      <span class="vc-label-text">
          {{{it.label || it.value}}}
      </span>
    </label>
  </div>
</template>

<script>
  import {componentBaseParamConfig, alias, name2Alias} from '../base-config';

  window.x = 1;

  export default {
    props: Object.assign({}, componentBaseParamConfig, alias, {
      data: {
        type: Array,
        default: function() {
          return [{
            value: false,
            label: ' '
          }];
        }
      },
      resultList: {
        type: Array
      },
      isVertical: {
        type: Boolean,
        default: false
      },
      value: {}
    }),

    beforeCompile() {
      name2Alias(this.data, this.asValue, this.asLabel);
    },

    compiled() {
      this.appendStyle = Object.assign({}, this.appendStyle, {
        display: this.isVertical ? 'block' : 'inline-block'
      })
    },

    data () {
      return {
        isDisabled: '',
        isShow: true
      }
    },

    watch: {
        data() {
          // 
          name2Alias(this.data, this.asValue, this.asLabel);
          this.isShow = false;
          setTimeout(() => {
            this.isShow = true;
          }, 30)
        }
    },

    methods: {
      toggleSwitch(index, value) {
        if(this.data[index].isDisabled) return;

        if(this.resultList) {
          if(!this.resultList.includes(value)) {
            this.resultList.push(value);
          } else {
            this.resultList.$remove(value);
          }

          this.value = this.resultList[0];
        } else {
          this.value = !this.value;
        }
      }
    }
  }
</script>

<style lang="scss">
  @import "style.scss";
</style>


