<!--
   ,

  @param {Number} number ,10099+
  @param {Boolean} isShowNumber ,
  @param {Object} appendClass class
  @param {Object} appendStyle Style
-->
<template>
    <span class="vc-badge-wrap">
        <slot></slot>
        <span v-if="number > 0 || !isShowNumber" :style="appendStyle"
              :class="[appendClass, {'vc-badge': isShowNumber, 'vc-badge-dot': !isShowNumber}]">
            {{isShowNumber ? (+number > 100 ? '99+' : number) : ''}}
        </span>
    </span>
</template>

<script>
    import {componentBaseParamConfig} from '../base-config';

    export default {
        props: Object.assign({}, componentBaseParamConfig, {
            number: {
                type: Number,
                default: 0
            },
            isShowNumber: {
                type: Boolean,
                default: false
            }
        })
    }
</script>

<style lang="scss">
    @import "style.scss";
</style>


