<!--
    message window.alert confirm 
    @require dialog
    :

    ex:
        <v-message-box :id="'mb1'" type="info" :title="''" :explain="''" :ok-fn="dialogCallbackFn1"></v-message-box>

    
    vm.$root.$$messageBox.yourId.show();

    @param {String}     type ,:info, success, confirm, error
    @param {String}     title 
    @param {String}     explain 
    @param {String}     cancelText 
    @param {String}     okText 
    @param {String}     isShow 
    @param {Function}   onOk ok
    @param {Function}   onCancel cancel
    @param {Object}     appendClass class
    @param {Object}     appendStyle Style
-->
<template>
    <pv-dialog :id="id"
              :append-class="appendClass"
              :append-style="appendStyle"
              :has-header="false"
              :on-cancel="onCancel"
              :on-ok="onOk"
              :cancel-text="cancelText"
              :has-cancel-btn="hasCancelBtn"
              :is-show.sync="isShow"
              :ok-text="okText" >

            <div class="vc-confirm-body">
                <i class=" vci {{iconClass}}"></i>
                <span class="vc-confirm-title">{{title}}</span>
                <div class="vc-confirm-content">{{explain}}</div>
            </div>
    </pv-dialog>
</template>

<script>
    import {componentBaseParamConfig} from '../base-config';
    import pvDialog from '../dialog';

    export default {
        props: Object.assign({}, componentBaseParamConfig, {
            id: {
                type: String,
                required: true
            },
            type: {
                type: String,
                default: 'info'
            },
            title: {
                type: String
            },
            explain: {

            },
            okText: {
                type: String,
                default: ' '
            },
            cancelText: {
                type: String,
                default: ' '
            },
            onOk: {
                type: Function
            },
            onCancel: {
                type: Function
            },
            isShow: {
                type: Boolean,
                default: false
            }
        }),

        data() {
            return {
                iconClass: {
                    info: 'vci-info-circle',
                    error: 'vci-exclamation-circle',
                    success: 'vci-check-circle',
                    confirm: 'vci-question-circle'
                }[this.type],
                hasCancelBtn: this.type === 'confirm'
            }
        },

        compiled() {
            this.appendClass = ' vc-confirm ' + this.appendClass;
        },

        ready() {
            this.$root.$$messageBox = this.$root.$$messageBox || {};
            this.$root.$$messageBox[this.id] = this.$root.$$dialog[this.id];
        },

        components: {
            pvDialog
        }
    }
</script>

<style lang="scss">
    @import "style.scss";
</style>


