<!--
   progress 

   @param {Number} progress ,[0, 100]
   @param {String} [size=normal|large] 
   @param {String} [type=normal|success|error|warn] 
   @param {String} title title,title
   @param {String} info ,title,
   @param {String} explain ,explain
   @param {Boolean} isShowText progress
   @param {Boolean} isActive progress
   @param {String} appendClass class
   @param {Object} appendStyle Style
-->

<template>
    <div :style="appendStyle" :class="['vc-progress', appendClass, typeClass, sizeClass, {'status-active': isActive, 'vc-progress-full': !isShowText}]">
        <!-- title -->
        <div class="vc-progress-title" v-if="!!title">
            <h3>{{title}}</h3>
            <span class="vc-progress-title-info">{{info || progress + '%'}}</span>
        </div><!-- title end -->

        <!-- line -->
        <div class="vc-progress-line-wrap">
            <span class="vc-progress-line-text" v-if="isShowText">{{progress + '%'}}
                <slot></slot>
            </span>
            <div class="vc-progress-line">
                <span class="vc-progress-line-bg" :style="progressStyle"></span>
            </div>
        </div><!-- line end -->

        <!-- explain -->
        <div class="vc-progress-explain" v-if="explain">{{explain}}</div>
    </div>
</template>

<script>
    import {componentBaseParamConfig} from '../base-config';

    export default {
        props: Object.assign({}, componentBaseParamConfig, {
            type: {
                type: String,
                default: 'default'
            },
            size: {
                type: String,
                default: 'normal'
            },
            title: {
                type: String
            },
            info: {
                type: String
            },
            explain: {
                type: String
            },
            progress: {
                type: Number
            },
            isShowText: {
                type: Boolean,
                default: false
            },
            isActive: {
                type: Boolean,
                default: false
            }
        }),

        data() {
            return {
                typeClass: {
                    'default': '',
                    'success': 'status-success',
                    'error': 'status-error',
                    'warn': 'status-warning'
                }[this.type],
                sizeClass: {
                    'normal': '',
                    'large': 'vc-progress-lg'
                }[this.size]
            }
        },
        computed: {
            progressStyle() {
                return {
                    width: this.progress + '%'
                }
            }
        }
    }
</script>

<style lang="scss">
    @import "style.scss";
</style>


