<!--
   

   @param {String} label 
   @param {} labelCol labelgrid,[0, 24]
   @param {} wrapperCol inputgrid,[0, 24]
   @param {Boolean} isRequire ,
   @param {String} explainType=error|success|warning|feedback 
   @param {Boolean} isExplainShow 
   @param {String} explainText 
-->

<template>
    <div :class="['vc-form-item', {'vc-form-item-with-help': isExplainShow}]">
        <label :class="['vc-col-' + labelCol, {'vc-form-item-required': isRequire}]">{{label}}</label>
        <div :class="['vc-form-item-control', explainClass, 'vc-col-' + wrapperCol]">
            <slot></slot>
            <div class="vc-form-explain">{{explainText}}</div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            label: {
                type: String,
                default: ''
            },

            labelCol: {
                default: ''
            },

            wrapperCol: {
                default: ''
            },

            isRequire: {
                type: Boolean,
                default: false
            },
            explainType: {
                type: String,
                default: ''
            },
            isExplainShow: {
                type: Boolean,
                default: false
            },
            explainText: {
                type: String,
                default: ''
            }
        },

        computed: {
            explainClass() {
                return {
                    error: 'has-error',
                    success: 'has-success',
                    warning: 'has-warning',
                    feedback: 'has-feedback is-validating'
                }[this.explainType]
            }
        },

        data () {
            return {}
        }
    }
</script>

<style lang="scss">
    @import "style.scss";
</style>


