<!--
    

   @param {Array} data 
   ex:
        data: [{
                order: 1,
                title: '',
                explain: '',
                isFinished: true
            }, {
                order: 2,
                title: '',
                explain: '',
                isActive: true
            }, {
                order: 3,
                title: '',
                explain: ''
            }]
   @param {String} [size=normal|small] 
   @param {Boolean} isVertical 
   @param {String} appendClass class
   @param {Object} appendStyle Style
-->

<template>
    <ul :style="appendStyle" :class="['vc-step', appendClass, sizeClass, {'vc-step-vertical': isVertical}]">
        <li v-for="it in data" :class="['vc-step-item', {'vc-step-item-finish': it.isFinished, 'vc-step-item-active': it.isActive}]">
            <i class="vc-step-item-line"></i>
            <span class="vc-step-item-order">{{it.order}}</span>
            <h3 class="vc-step-item-title">{{it.title}}</h3>
            <p class="vc-step-item-explain">{{it.explain}}</p>
        </li>
    </ul>
</template>

<script>
    import {componentBaseParamConfig} from '../base-config';

    export default {
        props: Object.assign({}, componentBaseParamConfig, {
            data: {
                type: Array
            },
            size: {
                type: String,
                default: 'normal'
            },
            isVertical: {
                type: Boolean,
                default: false
            }
        }),

        data() {
            return {
                sizeClass: {
                    'normal': '',
                    'small': 'vc-step-sm'
                }[this.size]
            }
        }
    }
</script>

<style lang="scss">
    @import "style.scss";
</style>


